<template>
  <div class="HomePage">
    
    <el-container>
        <el-header>
            <el-menu
            :default-active="activeIndex2"
            class="el-menu-demo"
            mode="horizontal"
            @select="handleSelect"
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#ffd04b">
            <el-menu-item index="1">处理中心</el-menu-item>
            <el-submenu index="2">
                <template slot="title">我的工作台</template>
                <el-menu-item index="2-1">选项1</el-menu-item>
                <el-menu-item index="2-2">选项2</el-menu-item>
                <el-menu-item index="2-3">选项3</el-menu-item>
                <el-submenu index="2-4">
                <template slot="title">选项4</template>
                <el-menu-item index="2-4-1">选项1</el-menu-item>
                <el-menu-item index="2-4-2">选项2</el-menu-item>
                <el-menu-item index="2-4-3">选项3</el-menu-item>
                </el-submenu>
            </el-submenu>
            <el-menu-item index="3" disabled>消息中心</el-menu-item>
            <el-menu-item index="4"><a href="" target="_blank">订单管理</a></el-menu-item>
            <!--得分数据模型score-->
            <span style="color: black; font-size: 50px; float: right;">得分：{{score}}</span>
            </el-menu>
        </el-header>
        <el-main>
          <template>
            <ul class="infinite-list"  style="overflow:auto">
              <!--遍历数据  还差多选按钮选中就减去对应的分数（score） 方法没定义-->>
              <li v-for="(c,i) in count" class="infinite-list-item" v-bind:key="i">
                <h1>{{c.table}}</h1>
                <p v-for="(option,j) in count[i].options" v-bind:key="j">
                  <span>{{option}}</span>                
                </p>
              </li>
            </ul> 
          </template>
        </el-main>
        <el-footer>
            <el-steps :active="active" finish-status="success">
                <el-step title="步骤 1"></el-step>
                <el-step title="步骤 2"></el-step>
                <el-step title="步骤 3"></el-step>
                <el-step title="步骤 4"></el-step>
                <el-step title="步骤 5"></el-step>
                <el-step title="步骤 6"></el-step>
                </el-steps>
            <el-button style="margin-top: 12px;" @click="next">下一步</el-button>
        </el-footer>
        <div class="block">
          <div class="radio">
            排序：
            <el-radio-group v-model="reverse">
              <el-radio :label="true">倒序</el-radio>
              <el-radio :label="false">正序</el-radio>
            </el-radio-group>
          </div>

          <el-timeline :reverse="reverse">
            <el-timeline-item
              v-for="(activity, index) in activities"
              :key="index"
              :timestamp="activity.timestamp">
              {{activity.content}}
            </el-timeline-item>
        </el-timeline>
      </div>

    </el-container>
    
  </div>
</template>

<script>
export default {
  name: 'HomePage',
  data () {
    return {
        score:100,
        activeIndex: '1',
        activeIndex2: '1',
        active: 0,
        textData:[{
            table: ""
        }],
        count: [{
          table:"（一）思想政治表现",
          options:["参与有损于祖国尊严、荣誉、利益和危害社会秩序活动的，经查实，减20分；","没有事前请假，无正当理由不参加学校、二级学院要求的集体政治学习、班团活动和其他集体活动的，经查实，减2分/次；","出现其他不良思想政治表现行为的，经查实，根据负面影响程度，减2-10分/次。"]
          
        }],
        reverse: true,
        activities: [{
          content: '活动按期开始',
          timestamp: '2018-04-15'
        }, {
          content: '通过审核',
          timestamp: '2018-04-13'
        }, {
          content: '创建成功',
          timestamp: '2018-04-11'
        }]
    }
    
  },
   methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      //进度条方法5为条数
      next() {
        if (this.active++ > 5) this.active = 0;
      },
      load () {
        this.count += 2
      },
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-header, .el-footer {
    /* background-color: #B3C0D1; */
    /* color: #333; */
    /* text-align: center; */
    /* line-height: 60px; */
  }
  
  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 200px;
  }
  
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }
  
  body > .el-container {
    margin-bottom: 40px;
  }
  
  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }
  
  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
</style>



